@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
*, *::after, *::before {
  box-sizing: border-box;
 }



html, body {
  font-size: 100%;
  scroll-behavior: smooth;
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background: #141414;
  color:#fff;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 10px;
  line-height: 1.2;


}

main { 
  margin : 0;
  padding: 0;;
}


/* Header */


.header {
  width: 100%;
  padding: 0 4%;
  display: flex;
  align-items: center;
  height: 68px;
  font-size: 1.2rem;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.7) 10%, rgba(0, 0, 0, 0));
  z-index: 10;
  top: 0;
  left: 0;
  position: relative;
  transition: background-color 400ms;
}

.header.pin-header {
  position: fixed;
  top: 0;
  background-color: black;
}


.header-height-pinned{
  height:68px;
}



.logo {
  display:flex;
  align-items: center;
  margin-right: 5px;
}

.logo img {
  object-fit: contain;
  height: 25px;
}


.header-menu .header-list {
  margin:0;
  padding:0;
  list-style-type: none;
  display:flex;
  align-items: center;
}

.menu-trigger {
  display: none;
}

.header-menu .header-list .header-list-item {
  margin-left: 18px;
}

.header-menu .header-list .header-list-item a {
  text-decoration:none;
  color:inherit;
  cursor:pointer;
}

.header-menu .header-list .header-list-item a.active {
  font-weight: bold;
}

.header-options {
  display:flex;
  align-items: center;
  flex: 1 1;
  justify-content: flex-end;

}

.header-options div {
  cursor: pointer;
}

.searchBox {
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.75);
  border: solid 1px rgba(255, 255, 255, 0.85);
}

.searchBox span {
  margin-left: 2px;
}

.searchInput {
  color: #fff;
  display: inline-block;
  background: transparent;
  border: none;
  padding: 0;
  font-size: 14px;
  width: 0;
  opacity: 0;
  outline: none;
  transition: width .5s;
}


.searchBox .searchInput {
  width: 212px;
  padding: 7px 14px 7px 7px;
  opacity: 1;
}

.icon {
  padding: 5px;
  font-size: 1.3rem;
}

.header-options div:not(:last-child) {
  margin-right: 15px;
}

.header-options div svg {
  font-size: 1.2em;
}

.account-menu {
  position: relative;
}

.account-dropdown-menu {
  width: 100%;
  
  display:flex;
  align-items: center;
}

.caret {
  margin-left: 10px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color:#fff transparent transparent transparent;
}

.presentation {
  display:flex;
  align-items: center;
}

/* hero billboard */

/* BILLBOARD REWORK */


.billboard-base {
  position: absolute;
  background-color:#000;
  width: 100%;
  height: 56.25vw;
  z-index: 0;
  top:0;
}

.billboard-image-wrapper {
  position: relative;
 
}

.billboard-image-wrapper img {
  width: 100%;
  background-position: center center;
  background-size: cover;
  border: 0;
}

.billboard-vignette-bottom {
  background-image: linear-gradient(to bottom, rgba(20, 20, 20, 0) 0%, rgba(20, 20, 20, 0.15) 15%, rgba(20, 20, 20, 0.35) 29%, rgba(20, 20, 20, 0.58) 44%, #141414 68%,#141414 100%);
  background-size: 100% 100%;
  background-position: 0 top;
  background-repeat: repeat-x;
  background-color:transparent;
  width: 100%;
  height: 14.7vw;
  top: auto;
  bottom: -1px;
  opacity: 0.87;
  z-index: 8;
  position: absolute;
  left: 0;
  right: 0;
}

.billboard-image-wrapper .billboard-maturity-rating {
  position: absolute;
  right: 0;
  bottom: 35%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.billboard-maturity-rating span {
  border: solid 3px#dcdcdc;
  border-top-style: solid;
  border-right-style: solid;
  border-bottom-style: solid;
  border-left-style: solid;
  border-style: none none none solid;
  background-color:
  rgba(51, 51, 51, 0.6);
  font-size: 1.1vw;
  padding: .5vw 3.5vw .5vw .8vw;
  display: flex;
  align-items: center;
  height: 2.4vw;

}

.billboard-content-limits {
  padding-bottom: 35%;
  margin-bottom: 20px;
 
}

/* billboard information */

.billboard-title {
  min-height: 13.2vw;
position: relative;
margin-bottom: 1.2vw;
}

.billboard-title img {
  width: 90%;
  transform-origin: bottom left;
}

.billboard-information {
  position: absolute;
  top: 0;
  bottom: 35%;
  left: 4%;
  width: 36%;
  z-index: 10;
  display:flex;
  justify-content: flex-end;
  flex-direction: column;
}

.logo-and-text {
  width: 100%;
  transition: transform 1.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.suplemental {
  font-size: 1.6vw;
color:
#fff;
font-weight: bold;
transition: color 1s cubic-bezier(0.165, 0.84, 0.44, 1);
text-shadow: 2px 2px 4px
rgba(0, 0, 0, 0.45);
margin: 1vw 0;
display: flex;
align-items: center;
}

.synopsis {
  color:
#fff;
font-weight: normal;
line-height: normal;
width: 100%;
font-size: 1.4vw;
text-shadow: 2px 2px 4px
rgba(0, 0, 0, 0.45);
margin-top: .1vw;
}

.billboard-link {
  display: flex;
  margin-top: 1.5vw;
  white-space: nowrap;
}

.play-link {
  text-decoration: none;
}

.hasLabel {
  border-radius: 0.3rem;
  padding-left: calc(4rem / 2);
  padding-right: calc(4rem / 2);
  padding-top: 0.6rem;
  padding-bottom: 0.6rem;
  border: 0;
  cursor:pointer;

  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  position: relative;

  margin-right: 1rem;
  margin-bottom: 1rem;
}

.hasLabel span {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 2.4rem;
  word-break: break-word;
  color:#181818;
}


.billboard-vignette {
  transition: opacity 500ms;
  background: linear-gradient(77deg, rgba(0, 0, 0, 0.6) 0%,
rgba(0, 0, 0, 0) 85%);
  position: absolute;
  top: 0;
  left: 0;
  right: 26.09%;
  bottom: 0;
  opacity: 1;
}

.play-icon {
  margin-right: 10px;
  line-height: 0;
  word-break: break-word;
}

.play-link-secondary {
  background-color: rgba(133,133,133,0.6);
  word-break: break-word;
  font-weight: bold;
}

.play-link-secondary span {
  color:#fff;
}


/* SLIDER */


.sliderBox {
  z-index: 1;
  margin: 4.5vw 0; 
  padding: 0;
  position: relative;

}

.slider-header {
  line-height: 1.3;
  margin: 0;
}

.slider-header a {
  font-size: 1.4vw;
  color:#e5e5e5;
  font-weight: bold;
  margin: 0 4% 0.5em 4%;
  text-decoration: none;
  display: flex;
  align-items: first baseline;

  min-width: 6em;
  cursor:pointer;
}

.slider-header a .see-more {
  margin-left: 1%;
  max-width: 0;
  line-height: 0.8vw;
  opacity: 0;
  font-size: .9vw;
  transition: max-width 1s, opacity 1s, transform 750ms;
  white-space: nowrap;
}

.slider-header a:hover .see-more {
  max-width: 200px;
  transform: translate(1vw, 0);
  opacity: 1;
}

.slider-header a .see-more-chevron {
  display: none;
  transition: transform 750ms;
  margin-left: 4px;
  font-weight: bold;
  font-size: 1vw;
}

.slider-header a:hover .see-more-chevron {
  /* content:'\e658' */
  display: block;
  transform: translate(1vw, 0);
  font-size: .8vw;
  line-height: .8vw;
}

.slider-container {
  position: relative;
  z-index: 3;
  transition: transform 300ms ease 100ms;

}

.slider { 
  z-index: 2;
  position: relative;
  margin: 0;
  padding: 0 4%;
}

.slider .sliderMask.showPeek {
  overflow-x: visible;
}


.slider .sliderMask {
  overflow-x: hidden;
  padding-bottom: 1px;
}


.slider .sliderMask .slider-content {
  white-space: nowrap;
}

/* SLIDER ITEM */


.slider .sliderMask .slider-content .slider-item:first-child {
  padding-left: 0;
}



.slider .slider-content .slider-item {
  width: 20%;
  box-sizing: border-box;
  z-index: 1;
  display: inline-block;
  position: relative;
  white-space: normal;
  vertical-align: top;
  padding: 0 2px;
  transition: transform 300ms ease 100ms, height 300ms ease 100ms;
  cursor: pointer;
  
}

.slider-item .boxart-container {
  width: 100%;
  height: 100%;
}


.boxart {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  padding: 28.125% 0;
}

.boxart img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.handle {
  opacity: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 20;
  width: 4%;
  text-align: center;
  justify-content: center;
  display: flex;
  color:#fff;
  background: rgba(20, 20, 20, 0.5);
  align-items: center;
  transition: all 0.3s ease-in-out;
}

.handle strong {
  /* display: none; */
  height: auto;
  font-size: 2.5vw;
}

.handle.handleNext {
  right: 0;
}

.handle.handlePrev {
  left: 0;
}

.slider:hover .handle {
  opacity:1;
}

/* .handle:hover strong{
  
} */

/* ZOOM */

.slider-content {
  transition: transform 400ms ease 200ms;
}

.slider-content:hover .slider-item:hover .show-details {
  opacity: 1;
  -webkit-animation: chevronSlideDown 0.8s;
          animation: chevronSlideDown 0.8s;
}

