/* JAWBONE MENU */

.ai-content-area-container .menu {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  font-size: 1vw;
  z-index: 100;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  pointer-events: none;
}

.ai-content-area-container .menu li {
  padding: 1em 1.5em 0;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  color: #ccc;
  min-width: 5%;
  pointer-events: auto;
}

.ai-content-area-container .menu .menu-button {
  margin: 0;
  padding: 0;
  letter-spacing: 1px;
  color: inherit;
  font-weight: bold;
  text-align: center;
  pointer-events: auto;
  text-decoration: none;
  cursor: pointer;
}

.ai-content-area-container .menu li span {
  height: .33em;
  display: block;
  width: 100%;
  margin-top: .66em;
}

.ai-content-area-container .menu li.current span {
  transform: translateY(0);
  background-color:#E50914;
}

.background-app { 
  overflow: hidden;
  z-index: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}


/* ITEM MARK */

.mark {
  border: solid 4px #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: -4px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
  z-index: 4;
}

.mark:before, .mark:after {
  position: absolute;
  width: 0;
  height: 0;
  content: '';
  top: 100%;
  left: 50%;
  margin-left: -13px;
  border-style: solid;
  border-width: 7px 13px 0 13px;
  z-index: 4;
}

.mark:before {
  border-color: rgba(0, 0, 0, 0.15) transparent transparent transparent;
  margin-top: 5px;
}

.mark:after {
  margin-top: 4px;
  border-color: #fff transparent transparent transparent;
}

/* details button */

.show-details {
  position: absolute;
  bottom: 0.1vw;
  opacity: 0;
  border:0;
  background: transparent;
  outline:none;
  width:100%;
  transform: translateY(0);
  transition: transform 0.4s, opacity 0.4s;
  transition-delay: 0.2s;
  z-index: 3;
}




.show-details span {
    display: block;
    margin: 0 auto;
    color: white;
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms;
    -webkit-animation-name: chevronSlideDown;
            animation-name: chevronSlideDown;
    -webkit-animation-duration: 400ms;
            animation-duration: 400ms;
    text-align: center;
    cursor: pointer;
}

.show-details span svg:hover {
  color: #E50914;
}

.show-details span svg {
  font-size:2rem;
}

@-webkit-keyframes chevronSlideDown {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes chevronSlideDown {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}


.jaw-bone-content.open .jaw-bone-open-container {
  opacity: 1;
}

