/* SUBMENU */

.navigation-menu {
  display: flex;
  align-items: center;
  font-weight: bold;
  height: 100%;
}

.menu-trigger:hover .submenu {
  opacity: 1;
  visibility: visible;
}

.navigation-menu:after {
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #fff transparent transparent transparent;
  margin-left: 5px;
}

.submenu {
  opacity: 0;
  cursor: default;
  visibility: hidden;
  margin-left: -90px;
  box-sizing: border-box;
  top: 71px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  font-size: 13px;
  line-height: 21px;
  border: solid 1px rgba(255, 255, 255, 0.15);
  transition-duration: 150ms;
}

.submenu .arrow {
  position: absolute;
  top: -16px;
  left: 50%;
  border-width: 7px;
  margin-left: -7px;
  border-color: transparent transparent #e5e5e5;
  border-style: solid;
  height: 0;
  width: 0;
}

.submenu .topbar {
  position: absolute;
  top: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background: #e5e5e5;
}

.submenu ul, .submenu ul li {
  padding: 0;
  list-style-type: none;
  margin: 0;
}

.submenu ul {
  cursor: default;
  height: auto;
}

.submenu ul li a {
  width: 260px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: #b3b3b3;
  transition: background-color 400ms;
}

.submenu ul li:hover  {
  background-color: rgba(255, 255, 255, 0.1);
}
